import axios from '@/utils/uniAxios.js'
export default {
	data(){
		return{
			username:'',
			leftIcon:'../../static/delivery/phone.png',
			code:'发送验证码',
			disabled:false,
			num:60,
			phone:'',
			authCode:''
		}
	},
	mounted() {
		
	},
	methods:{
		login(){
			var _this = this;
			let obj = {
				phone: _this.phone,
				authCode: _this.authCode,
				useType:1
			};
			if(!_this.phone){
				_this.$api.msg('请输入手机号')
			}else if(!_this.authCode){
				_this.$api.msg('请输入验证码')
			}else {
				axios.post('/sso/user/login',obj).then(res=>{
					if(res.data.code=='200'){
						var token = res.data.data.token;
						uni.setStorageSync('gt',token);
						uni.setStorageSync('deliveryPhone',_this.phone);
						uni.redirectTo({
							url:'set'
						})
						// uni.switchTab({
						// 	url:'set',
						// 	success:(res)=> { 
						// 		 let page = getCurrentPages();  //跳转页面成功之后
						// 		 if (!page){
						// 			 return
						// 		 }else{
						// 			 page[page.length-1].data.loadData()
						// 			 // var beforePage = page[0].data;
						// 			 //  page.loadData(); //如果页面存在，则重新刷新页面
						// 		 };   
						// 	  }
						// });
					}else{
						this.$api.msg(res.data.message)
					}
				})
			}
			
		},
		// 发送验证码
		postCode(){
			if (this.phone == '') {
				this.$api.msg('请输入手机号码')
			} else {
				var _this = this;
				let obj = {
					telephone: _this.phone
				}
				axios.post('/sso/user/sendCode',obj).then(res=>{
					console.log(res)
					if(res.data.code=='200'){
						_this.$api.msg('发送成功')
						if(res.data.code==200){
							_this.disabled = true;
							_this.code = _this.num + 's';
							var timer=setInterval(() => {
								_this.num--;
								_this.code = _this.num + 's';
								if (_this.num == 0) {
									clearInterval(timer)
									_this.code = '重新发送';
									_this.num = 60;
								}
							}, 1000)
						}
					}
				})
			}
		}
			
	}
}
